.woocommerce-error li,
.woocommerce-info,
.woocommerce-message,
div.woocommerce-error {
    background: #888;
    padding: 15px;
    color: #fff;
    line-height: normal;
    
    &:before {
        font-family: "icons-set";
        content: "\e90b";
        margin-right: 8px;
        font-size: 1.2em;
        vertical-align: middle;
    }
}

.woocommerce-error li,
div.woocommerce-error {
    background: #B53535;
    
    &:before {
        font-family: "icons-set";
        content: "\e90c";
        display: inline-block;
        margin-right: 10px;
        vertical-align: middle;
        font-size: 1.2em;
        color: #fff;
    }
}